body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html body {
  background: ivory;
}
.container {
    display: flex;
    min-height: 80%;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
    z-index: -1;
  }
  
  @media (min-width: 1100px) {
    .container {
      padding-left: 100px;
      padding-right: 100px;
      width: 1100px;
    }
  }

.gameCard {
    width: 150px;
    height: 150px;
    margin-right: 20px;
    margin-bottom: 20px;
    background-size: cover;
    box-shadow: 5px 5px 5px darkslategrey;
    border-radius: 5px;
    object-fit: none;
}

.gameCard:hover {
    border: 4px solid darkslategrey;
}

@media (max-width: 750px) {

    .gameCard {
        height: 100px;
        width: 100px;
    }
}

@media (max-width: 400px) {

    .gameCard {
        height: 75px;
        width: 75px;
    }
}
.jumbo {
    margin-top: 80px; 
}

.jumbotron {
    box-shadow: 0 2px 5px 0 #240b36;
    font-family: 'Patua One', cursive;
    font-size: 1.3em;
    font-weight: bold;
    background: -webkit-linear-gradient(right, #f1f2b5 0%, #c2e59c 100%);
    background: linear-gradient(to left, #f1f2b5 0%, #c2e59c 100%);
}

.instructions {
    text-align: center;
}
.header {
    background: -webkit-linear-gradient(left, #240b36 0%, #c31432 100%);
    background: linear-gradient(to right, #240b36 0%, #c31432 100%);
    width: 100%;
    min-height: 80px;
    color: ivory;
    position: fixed;
    top: 0;
    margin: 0 auto;
    box-shadow: 0 5px 5px #240b36;
    z-index: 1000;
}

.header ul {
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: center;
}

.header li {
    display: inline-block;
    width: 33%;
    padding-top: 20px;
    font-family: 'Chakra Petch', sans-serif;
}

.title {
    font-family: 'Lobster', cursive;
}

#score {
    font-size: 1.2em;
}


@media (max-width: 700px) {

    .header li {
        display: block;
        text-align: center;
        width: 100%;
        padding-top: 0;
    } 
}
.gameMessage {
    font-size: 1.5em;
    font-family: 'Ubuntu', sans-serif;
}

.black {
    color: ivory;
}

.correct {
    color: #c6ff1a;
}

.incorrect {
    color: darkred;
    -webkit-text-stroke: 1px black;
}
.footer {
    min-height: 80px;
    width: 100%;
    background: -webkit-linear-gradient(left, #240b36 0%, #c31432 100%);
    background: linear-gradient(to right, #240b36 0%, #c31432 100%);
    box-shadow: 0 0 5px 1px #240b36;
    position: relative;
    margin-top: 50px;
    margin: 0 auto;
    font-family: 'Chakra Petch', sans-serif;
}

.bottom {
    position: absolute;
    bottom: 0;
    margin: 0 auto;
    width: 100%;
    min-height: 50px;
    border-top: 5px solid #ff4e00(236, 0, 0);
    background: #ff4e00(224, 0, 0);
    text-align: center;
}

ul#footer-list {
    list-style-type: none;
    padding: 0;
    margin: 0 auto;
    width: 100%;
    height: 100%;
}

#footer-list li {
    display: inline-block;
    width: 33%;
    color: ivory;
}

#footer-list li a {
    color: ivory;
}

#footer-list li a:hover {
    font-size: 1.2em;
}

.logo {
    height: 30px;
    width: 30px;
}

#reactLogo {
    -webkit-animation:spin 10s linear infinite;
    animation:spin 10s linear infinite;
}
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }


@media (max-width: 600px) {

    .bottom {
        bottom: -30px;
    }

    #footer-list li {
        width: 100%;
        display: block;
        padding-bottom: 5px;
    }
}

@media (min-height: 860px) and (min-width: 1000px) {

    .footer {
        position: absolute;
        bottom: 0;
    }   
}
